// middleware/redirect.js
export default function ({ route, redirect }) {
    const redirects = {
      // '/': 'en/home',
    }
    
    if (redirects[route.path]) {
      return redirect(redirects[route.path])
    }
    else if (route.matched.length === 0) {
      // Si la ruta no coincide con ninguna existente
      return redirect('/');
    }
  }
  