
const showToast = ({ state, commit }, message) => {
  if (state.toast.show) commit('hideToast')

  setTimeout(() => {
    commit('showToast', {
      color: 'black',
      message,
      timeout: 3000
    })
  })
}

const increment = ({ commit }) => {
  commit('increment')
}

export default {
  showToast,
  increment
}
