
export default {
  showToast: (state, toast) => {
    const { color, timeout, message } = toast

    state.toast = {
      message,
      color,
      timeout,
      show: true
    }
  },
  setLayout : (state, layout) => {
    state.layout = layout
  },
  setChargedPage : (state, pageCharge) => {
    state.pageCharge = pageCharge
  },
  setRatingColor : (state, rating) => {
    state.rating = rating
  },
  setLanguage : (state, language) => {
    state.language = language
    localStorage.setItem('language', language)
  },
  setTeamFilter : (state, payload) => {
    switch (payload.option) {
      case 1:
        state.teamFilter1 = payload.teamFilter
        break;
      case 2:
        state.teamFilter2 = payload.teamFilter
        break;
      case 3:
        state.teamFilter3 = payload.teamFilter
        break;
      case 4:
        state.teamFilter4 = payload.teamFilter
        break; 
    }
  },
  setNewFilter : (state, payload) => {
    switch (payload.option) {
      case 1:
        state.newFilter1 = payload.newFilter
        break;
      case 2:
        state.newFilter2 = payload.newFilter
        break;
      case 3:
        state.newFilter3 = payload.newFilter
        break;
      case 4:
        state.newFilter4 = payload.newFilter
        break;
      case 5:
        state.newFilter5 = payload.newFilter
        break 
    }
  },
  setNewsletterFilter : (state, newsletterFilter) => {
    state.newsletterFilter1 = newsletterFilter
  },
  setDialogContact : (state, dialogContact) => {
    state.dialogContact = dialogContact
  },
  setLawyers : (state, lawyers) => {
    state.lawyers = lawyers
  },
  setNews : (state, news) => {
    state.news = news
  },
  setPracticeAreas : (state, practiceAreas) => {
    state.practiceAreas = practiceAreas
  },
  setSections : (state, sections) => {
    state.sections = sections
  },
  increment(state) {
    state.counter++
  }
}
